import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import { List, ListItem, colors, ListItemText } from '@material-ui/core';
import Divider from '@material-ui/core/Divider';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import { Link } from "gatsby";
import { IconButton } from '@material-ui/core';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';
import GitHubIcon from '@material-ui/icons/GitHub';
import MuiLink from '@material-ui/core/Link';
import { globalHistory } from "@reach/router"

const useStyles = makeStyles(theme => ({
  root: {},
  item: {
    display: 'flex',
    paddingTop: 0,
    paddingBottom: 0
  },
  button: {
    color: colors.blueGrey[800],
    padding: '10px 8px',
    justifyContent: 'flex-start',
    textTransform: 'none',
    letterSpacing: 0,
    width: '100%',
    fontWeight: theme.typography.fontWeightMedium
  },
  icon: {
    color: theme.palette.icon,
    width: 24,
    height: 24,
    display: 'flex',
    alignItems: 'center',
    marginRight: theme.spacing(1)
  },
  active: {
    color: theme.palette.primary.main,
    fontWeight: theme.typography.fontWeightMedium,
    '& $icon': {
      color: theme.palette.primary.main
    },
    backgroundColor: '#f5f5f5'
  },
  sideFooter: {
    position: "fixed",
    bottom: theme.spacing.unit * 2,
    left: theme.spacing.unit * 3
  },
  link: {
    textDecoration: 'none'
  }
}));

const SidebarNav = props => {
  const classes = useStyles();
  console.log(globalHistory.location.pathname);
  return (
    <div>
      <List>
        {props.pages.map(function (page, index) {
          return (
            <Link to={page.href} className={classes.link}>
              <ListItem button key={page.title} className={(globalHistory.location.pathname === page.href ? classes.active : '')}>
                <ListItemIcon>{page.icon}</ListItemIcon>
                <ListItemText primary={page.title} />
              </ListItem>
            </Link>
          )
        })}
      </List>
      <Divider />
      <div className={classes.sideFooter}>
        <Divider />
        <MuiLink href={props.profileLinks.linkedIn} target="_blank">
          <IconButton size="large">
            <LinkedInIcon />
          </IconButton>
        </MuiLink>
        <MuiLink href={props.profileLinks.facebook} target="_blank">
          <IconButton size="large">
            <FacebookIcon />
          </IconButton>
        </MuiLink>
        <MuiLink href={props.profileLinks.twitter} target="_blank">
          <IconButton size="large">
            <TwitterIcon />
          </IconButton>
        </MuiLink>
        <MuiLink href={props.profileLinks.github} target="_blank">
          <IconButton size="large">
            <GitHubIcon />
          </IconButton>
        </MuiLink>
      </div>
    </div>
  );
};

SidebarNav.propTypes = {
  className: PropTypes.string,
  pages: PropTypes.array.isRequired
};

export default SidebarNav;